import React from "react";
import {
  Link,
  useMatch,
  useResolvedPath,
  useNavigate,
  Navigate,
} from "react-router-dom";

const Laman = ({ children, to }) => {
  let resolved = useResolvedPath(to);
  let match = useMatch({ path: resolved.pathname, end: true });
  return (
    <div>
      <Link
        className={
          match
            ? `lg:text-5xl font-semibold text-3xl`
            : `lg:text-3xl font-semibold`
        }
        to={to}
      >
        {children}
      </Link>
    </div>
  );
};

const Large = () => {
  let navigate = useNavigate();
  return (
    <div className="  ">
      <div className="hidden   lg:flex space-x-8 py-5 items-center z-10 pa fixed w-max lg:right-20 text-white">
        <Laman to="/home">Home</Laman>
        <Laman to="/services">Services</Laman>
        <Laman to="/about">About</Laman>
        <Laman to="/hire-me">Hire Me! </Laman>
      </div>
      <div className="lg:hidden  border-none bg-none right-1 space-x-8 py-5 items-center z-10 pa fixed w-max lg:right-20 text-white font-bold">
        <select
          onChange={(e) => {
        
            navigate(`${e.target.value}`);
          }}
          className="text-2xl font-bold  text-center bg-transparent outline-none  rounded-3xl appearance-none p-2 focus:bg-gray-700   "
          name="link"
          id="link"
        >
          <option value="/home">Home</option>
          <option value="/services">Services</option>
          <option value="/about">About</option>
          <option value="/hire-me">Hire Me</option>
        </select>
      </div>
    </div>
  );
};
const Responsive = () => {
  return <div></div>;
};
const Navbar = () => {
  const [device, setDevice] = "large";
  return <Large></Large>;
  // <div>{device === "large" ? <Large></Large> : <Responsive />}</div>;
};

export default Navbar;
